<template>
	<div class="body">
		<div class="cash_flow_table_box">
			<div class="cash_flow_table_title flex_between">
				<div class="flex_start">
					<span class="circle_blue"></span> 
					<span class="name">支出—管理费</span>
				</div>
			</div>
			<!--查看支出模块-->
			<div class="table">
				<el-table :data="tableData" border style="width: 100%">
					<el-table-column prop="" label="收入主体类别">
						<template slot-scope="scope">
							{{ type == 1?'母基金':'自管基金' }}
						</template>
					</el-table-column>
					<el-table-column prop="sg_company_manager_name" label="收入主体名称">
					</el-table-column>
					<el-table-column prop="event" label="事件">
					</el-table-column>
					<el-table-column prop="should_subscribe" label="应缴管理费（万元）">
					</el-table-column>
					<el-table-column prop="subscribe" label="实缴管理费（万元）">
					</el-table-column>
					<el-table-column prop="unit" label="币种">
						<template slot-scope="scope">
							{{ scope.row.unit == 1?'人民币':'美元' }}
						</template>
					</el-table-column>
					<template slot="empty">
						<div class="table-nodata">
							<img src="../../images/table_nodata.png"> 没有对应数据
						</div>
					</template>
				</el-table>
			</div>
		</div>
	</div>
</template>

<script>
	import { dialogConfirm, dialogConfirmHasbtn } from '../../config/mUtils'
	import { getMotherPayoutManageCashApi } from '../../config/api'
	
	export default {
		data() {
			return {
				id: '',
				type: 1,//1代表母基金模块；2代表自管基金模块
				count:'',
				tableData: [],
			}
		},
		created() {
			this.id = this.$route.query.id;
			this.type = this.$route.query.type;
			this.count = this.$route.query.count;
			this.getInitData()
		},
		computed: {
	
		},
		mounted() {},
		methods: {
			getInitData(){
				getMotherPayoutManageCashApi({
					fof_id: this.id,
					count: this.count,
					difference_type: this.type,
				}).then(res => {
					console.log(res);
					if(res.data.msgNo == 1) {
						this.tableData.push(res.data.payload.data);
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			}
		}
	}
</script>

<style lang="scss" scoped>
	.body {
		padding: 0 0px 30px 0px;
	}
	
	
	.cash_flow_table_title {
		font-size: 14px;
		color: #6292F4;
		margin: 22px 0px;
		.ts {
			color: #FF3B30;
			margin-left: 23px;
		}
		.name {
			color: #6292F4;
		}
	}
	
	.circle_blue {
		width: 7px;
		height: 7px;
		border-radius: 50%;
		margin-right: 5px;
		margin-left: 8px;
		background-color: #6292F4;
	}
</style>


